<template lang="pug">
.curso-main-container.pb-3
  BannerInterno
  .container.tarjeta.tarjeta--blanca.p-4.p-md-5.mb-5
    #t_1_2
    .titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 1.2. Pronósticos meteorológicos y tipos de ruta

    .row.justify-content-center.align-items-center.mb-2(data-aos="fade-left")
      .col-md-3
        figure
          img(src='@/assets/curso/temas/tema1/tema1/tema1-img2.svg', alt='El diseño y la organización de paquetes turísticos')

      .col-md-8.mb-4.mb-md-0.my-2
        p La planificación de un recorrido turístico requiere considerar diversos factores que garanticen el bienestar y la seguridad de los usuarios. Entre ellos, el clima y las características de la ruta son determinantes para el éxito de la experiencia. Anticipar las condiciones meteorológicas y seleccionar adecuadamente el tipo de ruta permite prevenir contratiempos, cuidar el entorno y ofrecer un servicio de calidad. La relación entre ambos aspectos define el modo en que se desarrolla la actividad turística y la manera en que se aprovechan los recursos naturales y culturales del territorio.
        h3 Pronósticos meteorológicos
        p El pronóstico meteorológico es un factor determinante en la planificación de los recorridos turísticos, ya que las condiciones climáticas influyen directamente en la seguridad, la movilidad y el disfrute de los usuarios. Conocer el estado del tiempo permite tomar decisiones acertadas sobre los horarios de salida, los medios de transporte, los puntos de parada y los elementos de protección necesarios. En la organización logística, el clima no se considera un imprevisto, sino una variable que debe analizarse y anticiparse con responsabilidad. Ahora profundizaremos en la importancia de consultar dichos reportes:

    .tarjeta.tarjeta--azul.p-4.pt-3.tarjeta-slyder-tipo-a-b(data-aos="fade-right")
      SlyderA(tipo="b")
        .row.justify-content-center.align-items-center.my-1
          .col-md-6.mb-4.mb-md-0
            h2.color-primario Consultar el clima
            h4.color-adicional-4 Consultar y comprender los pronósticos meteorológicos implica revisar información actualizada proveniente de fuentes confiables, como el Instituto de Hidrología, Meteorología y Estudios Ambientales (IDEAM) o los servicios meteorológicos locales. Estas entidades ofrecen reportes diarios y alertas sobre lluvias, temperaturas, vientos, radiación solar y fenómenos naturales que pueden afectar la operación turística. 
          
          .col-md-5
            figure
              img(src='@/assets/curso/temas/tema1/tema2/rec5-img1.svg', alt='Slide 1')

        .row.justify-content-center.align-items-center.my-1
          .col-md-6.mb-4.mb-md-0
            h2.color-primario El rol del guía
            .h4.color-adicional-4 Un guía o coordinador logístico debe interpretar dichos reportes y adaptar el recorrido a las condiciones previstas.
            h4.color-adicional-4 Por ejemplo, en temporada de lluvias es recomendable programar salidas en la mañana, usar rutas pavimentadas y llevar capas impermeables; en época seca, se prioriza la hidratación, el uso de protector solar y sombreros.

          .col-md-5
            figure
              img(src='@/assets/curso/temas/tema1/tema2/rec5-img2.svg', alt='Slide 2')
        
        .row.justify-content-center.align-items-center.my-1
          .col-md-6.mb-4.mb-md-0
            h2.color-primario Perspectiva OMT
            h4.color-adicional-4 La Organización Mundial del Turismo (OMT, 2020) señala que “una adecuada gestión del clima en los itinerarios turísticos es un componente clave de la seguridad y la calidad del servicio, pues permite minimizar riesgos y aprovechar las condiciones ambientales de forma sostenible”. 

          .col-md-5
            figure
              img(src='@/assets/curso/temas/tema1/tema2/rec5-img3.svg', alt='Slide 3')

    br
    .row.justify-content-center.align-items-center.mb-2(data-aos="fade-left")
      p En efecto, prever el clima no solo mejora la experiencia del visitante, sino que evita cancelaciones, accidentes o afectaciones a los ecosistemas locales. Además, permite planificar pausas activas, zonas de sombra o abrigo y rutas de evacuación en caso de emergencia.
      h3 Tipos de ruta
      p Una ruta turística se define como el recorrido planificado que conecta uno o varios atractivos turísticos, integrando servicios, actividades y experiencias que le otorgan sentido al desplazamiento. No se trata únicamente del trayecto físico, sino del conjunto de elementos que articulan la visita: puntos de interés, medios de transporte, tiempos estimados, pausas activas y condiciones del entorno. La ruta es, en esencia, el hilo conductor de la experiencia turística.
      p El diseño de una ruta adecuada permite optimizar el tiempo, mejorar la orientación, fortalecer la seguridad y garantizar una experiencia coherente con los intereses del visitante. Su elección depende de factores como la duración del recorrido, la accesibilidad, las condiciones climáticas, el perfil del usuario y la naturaleza del destino.
      p Entre los tipos más comunes de rutas se encuentran:

    .row.justify-content-center
      .col-lg-12
        .crd.tarjeta.acordeonA
          PasosB.color-acento-botones.p-4(data-aos="fade-right")
            .row.justify-content-center(titulo="Ruta lineal").pt-1
              .col-lg-10.mb-4.mb-lg-0
                .color-adicional-4 En una ruta lineal, el recorrido se realiza desde un punto de partida hasta un punto de llegada diferente, sin regresar al sitio de origen. Este tipo de itinerario permite recorrer grandes distancias y descubrir una secuencia de paisajes, atractivos o comunidades a lo largo del trayecto. Su planificación requiere una logística cuidadosa, ya que deben contemplarse aspectos como el transporte de regreso, el alojamiento intermedio y la asistencia en caso de emergencia.
                p.color-adicional-4 Para garantizar el éxito de una ruta lineal, es fundamental establecer una coordinación eficiente del transporte, asegurando que los viajeros cuenten con vehículos de apoyo o servicios de retorno al punto de origen. Asimismo, se recomienda implementar señalización visible, puntos de descanso seguros y protocolos de comunicación entre guías y operadores, de modo que se mantenga el control del grupo y la seguridad durante todo el trayecto.

              .col-lg-2.col-6
                br
                figure
                  img(src='@/assets/curso/temas/tema1/tema2/rec6-img1.svg', alt='')

            .row.justify-content-center(titulo="Ruta fluvial Puerto Nariño - Isla de los Micos - Leticia (Amazonas)").pt-1
              .col-lg-10.mb-4.mb-lg-0
                h5.color-adicional-4 Ruta fluvial Puerto Nariño - Isla de los Micos - Leticia (Amazonas)
                .color-adicional-4 Ejemplo ruta lineal
                p.color-adicional-4 Descripción: El recorrido inicia en el municipio de Puerto Nariño, donde los visitantes abordan un bote para navegar por el río Amazonas.La primera parada es la Isla de los Micos, donde se realiza una actividad interpretativa sobre fauna silvestre y conservación.Finalmente, el recorrido termina en la ciudad de Leticia, punto de llegada donde los turistas descansan y registran su experiencia.El trayecto es unidireccional y requiere coordinación logística con los transportadores fluviales y guías locales..

              .col-lg-2.col-6
                figure
                  img(src='@/assets/curso/temas/tema1/tema2/rec6-img2.svg', alt='')

            .row.justify-content-center(titulo="Ruta Circular").pt-1
              .col-lg-10.mb-4.mb-lg-0
                .color-adicional-4 Es aquella en la que el recorrido inicia y termina en el mismo punto, pasando por diferentes sitios intermedios que conforman un circuito turístico. Este tipo de ruta permite optimizar tiempos, recursos y la experiencia del visitante, ya que los desplazamientos están planificados para regresar al punto de origen.
                p.color-adicional-4 También es esencial garantizar una planificación detallada del circuito, definiendo con precisión los puntos de parada, los tiempos de permanencia y las actividades en cada sitio. Se recomienda establecer un punto de control o centro logístico en el lugar de inicio y cierre del recorrido, desde donde se coordine el transporte, el registro de usuarios y la atención a emergencias. Además, debe asegurarse que las vías de conexión estén debidamente señalizadas y que los guías cuenten con información actualizada sobre el tráfico, las condiciones del terreno y los servicios disponibles.
                
              .col-lg-2.col-6
                figure
                  img(src='@/assets/curso/temas/tema1/tema2/rec6-img3.svg', alt='')

            .row.justify-content-center(titulo="Salento - Valle de Cocora Filandia - Salento (Quindío)").pt-1
              .col-lg-10.mb-4.mb-lg-0
                h5.color-adicional-4 Salento - Valle de Cocora - Filandia - Salento (Quindío)
                .color-adicional-4 Ejemplo ruta circular
                p.color-adicional-4 Descripción: El recorrido inicia en el municipio de Salento, punto de encuentro y salida del grupo. Desde allí se viaja hacia el Valle de Cocora, reconocido por sus palmas de cera y senderos ecológicos. Los turistas realizan caminatas guiadas, observación de aves y sesiones fotográficas. Posteriormente, el recorrido continúa hacia Filandia, un pueblo típico del Paisaje Cultural Cafetero, donde los visitantes disfrutan de un almuerzo tradicional y una visita al mirador de la colina iluminada.Finalmente, se retorna a Salento, completando un circuito cerrado. Este tipo de ruta facilita la logística del transporte, favorece la sostenibilidad y reduce desplazamientos innecesarios.

              .col-lg-2.col-6
                figure
                  img(src='@/assets/curso/temas/tema1/tema2/rec6-img4.svg', alt='')
            
            .row.justify-content-center(titulo="Ruta Radial").pt-1
              .col-lg-10.mb-4.mb-lg-0
                .color-adicional-4 Una ruta radial se caracteriza por tener un punto central o base desde el cual se realizan diferentes desplazamientos hacia atractivos cercanos. Al finalizar cada visita, los turistas regresan al punto inicial. Este tipo de ruta es ideal para planear actividades diarias partiendo desde un mismo alojamiento o centro de operaciones, lo que facilita la logística y el control del grupo.
                p.color-adicional-4 En las rutas radiales, la principal recomendación es definir claramente el punto base, garantizando que cuente con servicios básicos de alojamiento, alimentación, transporte y primeros auxilios. Cada salida debe planificarse considerando la distancia, el tiempo de desplazamiento y las condiciones climáticas. Es importante establecer horarios fijos de salida y regreso, así como mantener comunicación permanente entre guías y personal de apoyo en el punto base.

              .col-lg-2.col-6
                figure
                  img(src='@/assets/curso/temas/tema1/tema2/rec6-img5.svg', alt='')

            .row.justify-content-center(titulo="San Gil (Santander) como centro de turismo de aventura").pt-1
              .col-lg-10.mb-4.mb-lg-0
                h5.color-adicional-4 San Gil (Santander) como centro de turismo de aventura
                .color-adicional-4 Ejemplo ruta radial
                h5.color-adicional-4 Descripción:
                .color-adicional-4 El municipio de San Gil actúa como el punto base desde el cual los visitantes realizan actividades y excursiones hacia los alrededores:
                ol.lista-ol--cuadro
                  li 
                    .lista-ol--cuadro__vineta.clr-color-adicional-4
                      span
                    span #[strong Parque Nacional del Chicamocha:] teleférico, miradores y deportes extremos.
                  li 
                    .lista-ol--cuadro__vineta.clr-color-adicional-4
                      span  
                    span #[strong Curití:] talleres artesanales y rutas de senderismo.
                  li 
                    .lista-ol--cuadro__vineta.clr-color-adicional-4
                      span  
                    span #[strong Río Fonce:] rafting y gastronomía local. Este tipo de ruta optimiza recursos, reduce el impacto ambiental del transporte y promueve la economía local en municipios cercanos.

              .col-lg-2.col-6
                figure
                  img(src='@/assets/curso/temas/tema1/tema2/rec6-img6.svg', alt='')

            .row.justify-content-center(titulo="Ruta Temática").pt-1
              .col-lg-10.mb-4.mb-lg-0
                br
                .color-adicional-4 Una ruta temática se construye alrededor de un eje conceptual o narrativo que da sentido al recorrido. Su objetivo no es solo visitar lugares, sino contar una historia o destacar un aspecto particular de la cultura, la naturaleza o la identidad local. Las rutas temáticas pueden centrarse en gastronomía, patrimonio histórico, biodiversidad, artesanía o tradiciones ancestrales.
                p.color-adicional-4 El diseño de este tipo de ruta requiere investigación, creatividad y articulación entre actores del territorio (comunidades, instituciones, prestadores turísticos y guías). Una buena ruta temática combina información interpretativa, experiencias vivenciales y actividades participativas que conectan emocionalmente al visitante con el tema elegido.
                
              .col-lg-2.col-6
                figure
                  img(src='@/assets/curso/temas/tema1/tema2/rec6-img7.svg', alt='')

            .row.justify-content-center(titulo="Ruta Temática").pt-1
              .col-lg-10.mb-4.mb-lg-0
                p.color-adicional-4 Se debe priorizar la coherencia del relato interpretativo y la calidad de la experiencia. Es importante diseñar guiones o narrativas que unifiquen el recorrido y destaquen los valores culturales, naturales o patrimoniales del tema central. Se recomienda articular la ruta con actores locales —portadores de saberes, artesanos, cocineras o músicos—, garantizando su participación activa y un trato justo por su trabajo. Asimismo, deben implementarse protocolos de respeto cultural, uso adecuado de los espacios y prácticas sostenibles que eviten la sobreexplotación de recursos o la alteración de costumbres comunitarias.

              .col-lg-2.col-6
                figure
                  img(src='@/assets/curso/temas/tema1/tema2/rec6-img8.svg', alt='')
            
            .row.justify-content-center(titulo="“Sabores del Pacífico” Buenaventura y comunidades afro del Valle del Cauca").pt-1
              .col-lg-10.mb-4.mb-lg-0
                h5.color-adicional-4 “Sabores del Pacífico” - Buenaventura y comunidades afro del Valle del Cauca
                .color-adicional-4 Ejemplo ruta temática 
                p.color-adicional-4 Descripción: Esta ruta se desarrolla en torno a la gastronomía tradicional del litoral Pacífico colombiano, explorando los saberes culinarios de las comunidades afrodescendientes. El recorrido inicia en Buenaventura, donde los visitantes conocen el mercado local y degustan platos típicos como el encocado de piangua y el tapao. Posteriormente, se visita Juanchaco y Ladrilleros, donde cocineras tradicionales enseñan el uso de productos locales como el coco, el chontaduro y los pescados de la región. La experiencia finaliza con una actividad cultural en una comunidad ribereña, donde se combinan la música del currulao, la danza y la cocina ancestral como expresión de identidad y sostenibilidad.

              .col-lg-2.col-6
                figure
                  img(src='@/assets/curso/temas/tema1/tema2/rec6-img9.svg', alt='')

            .row.justify-content-center(titulo="Ruta de travesía o aventura").pt-1
              .col-lg-10.mb-4.mb-lg-0
                .color-adicional-4 La ruta de travesía o de aventura se caracteriza por implicar desplazamientos prolongados, contacto directo con la naturaleza y retos físicos o técnicos. Este tipo de ruta se desarrolla en entornos rurales, selváticos o de montaña, donde el terreno presenta condiciones variables que exigen preparación, equipos adecuados y acompañamiento especializado.
                .color-adicional-4 Su propósito no es únicamente el desplazamiento, sino la vivencia del recorrido: el esfuerzo, la observación de paisajes, la interacción con comunidades locales y la conexión con el entorno natural.
                p.color-adicional-4 La seguridad y la preparación técnica son prioritarias. Antes de iniciar el recorrido, se debe realizar una evaluación del terreno, verificar el estado de los equipos y asegurar que los participantes cuenten con las condiciones físicas y los implementos necesarios. Es indispensable contar con guías certificados en primeros auxilios y rescate, así como con un plan de contingencia ante condiciones climáticas adversas.
              
              .col-lg-2.col-6
                figure
                  img(src='@/assets/curso/temas/tema1/tema2/rec6-img10.svg', alt='')

            .row.justify-content-center(titulo="Camino Teyuna - Ciudad Perdida (Sierra Nevada de Santa Marta)").pt-1
              .col-lg-10.mb-4.mb-lg-0
                h5.color-adicional-4 Camino Teyuna - Ciudad Perdida (Sierra Nevada de Santa Marta)
                .color-adicional-4 Ejemplo ruta de travesía
                p.color-adicional-4 Descripción: El recorrido parte desde El Mamey y asciende durante cuatro o cinco días por senderos ancestrales hasta llegar a la Ciudad Perdida, uno de los principales sitios arqueológicos de Colombia. La ruta atraviesa ríos, montañas y zonas selváticas, combinando la aventura con la interpretación cultural de los pueblos indígenas Kogui y Wiwa. Durante la travesía, los turistas realizan caminatas diarias, descansan en campamentos y aprenden sobre la cosmovisión indígena y la conservación del territorio.
                
              .col-lg-2.col-6
                figure
                  img(src='@/assets/curso/temas/tema1/tema2/rec6-img11.svg', alt='')



    br
    .row.justify-content-center.align-items-center.mb-2(data-aos="fade-left")
      p Este tipo de ruta fomenta el turismo sostenible y de bajo impacto, promoviendo el respeto por la naturaleza y las comunidades locales. Además, fortalece valores como la cooperación, la resistencia y la conciencia ambiental. Estas rutas requieren una planeación logística detallada que contemple aspectos como la seguridad, la hidratación, la alimentación, el transporte de equipos y los puntos de descanso o acampada. Por su naturaleza, están dirigidas a grupos con experiencia en caminatas, deportes extremos o ecoturismo de bajo impacto.
      p La selección de la ruta debe armonizar con las condiciones meteorológicas y la capacidad del grupo. En temporada de lluvias, por ejemplo, se recomiendan trayectos cortos y seguros, mientras que en climas secos es posible realizar recorridos más largos o con mayor grado de exigencia física. Como afirma el especialista Jorge Medina (2021), “el guía que consulta el cielo antes de partir demuestra más responsabilidad que el que solo mira el mapa”, recordando que la planificación consciente es la base de un turismo responsable, seguro y sostenible.



